.App {
  text-align: center;
  font-family:monospace;
  margin:0;
  height: 90%;
  scroll-behavior: smooth;
}

header {
  padding-bottom: 2%;
  text-align: center;
  color:#4A235A;
  background-color:white;
  }

.Profile-Main-Logo{
  padding-top: 2%;
  height:500px;
  text-align: center;
}

#Portfolio-button {
  font-weight: bolder;
    font-size: 20px;
    border:none;
    color:#4A235A;
    background-color:white;
    font-family: monospace;
  }

#Portfolio-button:hover{
  color:#F1C40F;
  cursor:pointer;
}

.Portfolio-section{
  padding-top: 3%;
  margin: 0;
  height: 100%;
  padding-left: 1%;
  padding-bottom: 5%;
}
.App-logo {
  height: 390px;
}

.Portfolio-header {
  height: 400%;
  color:  black;
  float: left;
  font-weight:30em;
}
.Profile-Card{
   -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
           box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
   max-width: 320px;
   margin: auto;
   text-align: center;
   background-color: #4A235A;
   color: #F1C40F;
   height:600%;
}

.p Title{
  color:  white;
  font-size: 15px;
}

.Profile-Card a{
   font-size: 15px;
   -webkit-text-decoration-color:#F1C40F;
           text-decoration-color:#F1C40F;
}

.App-title {
  font-size: 20px;
  }

.App-intro {
  font-size: large;
  color: #F1C40F;
  padding-top: 2%;
  font-weight: bold;
  padding-left: 10%;
  background-color: white;
}

.App-Main-Tab{
  overflow: hidden;
  color:  black;
  font-family: monospace;
  float:none;
  padding-right: none;
  padding-left: none;
  background-color: inherit;
  padding-top: 2%;
}

.App-Main-Tab button{
   float: center;
   border: none;
   outline: none;
   cursor: pointer;
   padding: 2% 2%;
   -webkit-transition: 0.3s;
   -o-transition: 0.3s;
   transition: 0.3s;
   color:white;
   font-weight: bold;
   font-family: monospace;
   background-color:#4A235A;
   font-size: 13px;
}

.App-Main-Tab button:hover{
   background-color: #F1C40F;
}

.App-Main-Tab button:active {
    background-color: #F1C40F;
}

.Tab-content {
    display: none;
    color: black;
    text-align: left;
    font-size: 15px;
    float:left;
    background-color: white;
    padding-left: 5%;
}

a:link, a:visited, a:active {
  color:#F1C40F;
  text-decoration: none;
  text-align: center;
  padding-right: 2%;
}

a:hover{
  color: #58D68D;
  text-decoration: none;
  text-align: center;
}

Resume-File-Link{
  color:black;
  text-align: center;
}

li{
  padding-bottom: 2%;
  width: 700px;
  list-style: none;
}

b{
  background-color:#D7BDE2;
}

i{
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}

.Arrow-Right{
  -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
   -webkit-transform: rotate(-45deg);
}

